import React from 'react';
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'
import WorkflowApprovalDetailsTable from "./Tables/WorkflowApprovalDetailsTable";
import WorkflowApprovalDetailsModal from "./Modals/WorkflowApprovalDetailsModal";

class WorkflowApprovalDetails extends React.Component {
    constructor(props, context) {
        super(props, context);
        
        this.state = {
            loggedInUser: props.user,
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,

            WORKFLOW_DETAILS_ID: 0,
            WORKFLOW_SETUPS_ID: 0,
            APPROVAL_DESCRIPTION: '',
            APPROVAL_COMMENT: '',
            APPROVED_BY: '',
            APPROVAL_STATUS: '',

            APPROVAL_TYPE: '',

            CREATED_BY: props.user.EMAIL,
            MODIFIED_BY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.populateData();

    }

    populateData() {
        const getdata = `          
            query{
                WorkFlowDetails {
                    WORKFLOW_DETAILS_ID
                    WORKFLOW_SETUPS_ID
                    WORKFLOW_SETUP {
                        WORKFLOW_SETUPS_ID
                        APPROVAL_SEQUENCE
                        APPROVAL_LEVEL
                        GROUP_USERS
                        WORKFLOW {
                            MODULE_TYPE {
                                MODULE_ID
                                MODULE_NAME
                            }
                        }
                    }
                    APPROVAL_DESCRIPTION
                    APPROVAL_COMMENT
                    APPROVED_BY
                    USER {
                        USER_ID
                        USER_NAME
                        EMAIL
                    }
                    APPROVAL_STATUS
                    CREATED_BY
                    MODIFIED_BY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => {

                let WorkFlowDetails = []

                data.data.WorkFlowDetails.forEach((workflowDetail, index) => {
                    if (workflowDetail.WORKFLOW_SETUP.GROUP_USERS && workflowDetail.WORKFLOW_SETUP.GROUP_USERS.includes(this.state.loggedInUser.USER_ID)) {
                        WorkFlowDetails.push(workflowDetail)
                    }
                });

              this.setState({
                data: WorkFlowDetails
              })
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Workflow Master Data');
                }
            })

    }

    handleSubmit = (event) => {
        event.preventDefault();
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
        
            const formEdit = `
                mutation{
                    updateWorkFlowDetails(
                        WORKFLOW_DETAILS_ID: ${this.state.WORKFLOW_DETAILS_ID},
                        WORKFLOW_SETUPS_ID: ${this.state.WORKFLOW_SETUPS_ID},
                        APPROVAL_DESCRIPTION: "${this.state.APPROVAL_DESCRIPTION}",
                        APPROVAL_COMMENT: "${this.state.APPROVAL_COMMENT}",
                        APPROVED_BY: ${this.props.user.USER_ID},
                        APPROVAL_STATUS: "${this.state.APPROVAL_STATUS ? this.state.APPROVAL_STATUS : 'PENDING'}",
                        MODIFIED_BY: "${this.state.MODIFIED_BY}",              
                        MODIFIED_DATE: "${this.state.MODIFIED_DATE}"
                    ){
                        WORKFLOW_DETAILS_ID
                    }
                }
            `;

            if (this.state.InsertUpdate !== 'Insert') {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteWorkFlowDetails(
                WORKFLOW_DETAILS_ID:${this.state.WORKFLOW_DETAILS_ID}
            ){
                WORKFLOW_DETAILS_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleEditData = (e) => {
        this.setState({
            WORKFLOW_DETAILS_ID: e.WORKFLOW_DETAILS_ID,
            WORKFLOW_SETUPS_ID: e.WORKFLOW_SETUPS_ID,
            APPROVAL_DESCRIPTION: e.APPROVAL_DESCRIPTION,
            APPROVAL_COMMENT: e.APPROVAL_COMMENT,
            APPROVED_BY: e.APPROVED_BY,
            APPROVAL_STATUS: e.APPROVAL_STATUS,
            APPROVAL_TYPE: e.WORKFLOW_SETUP.WORKFLOW.MODULE_TYPE.MODULE_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            WORKFLOW_DETAILS_ID: e.WORKFLOW_DETAILS_ID,
            WORKFLOW_SETUPS_ID: e.WORKFLOW_SETUPS_ID,
            APPROVAL_DESCRIPTION: e.APPROVAL_DESCRIPTION,
            APPROVAL_COMMENT: e.APPROVAL_COMMENT,
            APPROVED_BY: e.APPROVED_BY,
            APPROVAL_STATUS: e.APPROVAL_STATUS,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            WORKFLOW_DETAILS_ID: '',
            WORKFLOW_SETUPS_ID: '',
            APPROVAL_DESCRIPTION: '',
            APPROVAL_COMMENT: '',
            APPROVED_BY: '',
            APPROVAL_STATUS: '',
            APPROVAL_TYPE: '',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                console.log(item)
                let startsWithCondition =
                    (item.WORKFLOW_SETUP && item.WORKFLOW_SETUP.APPROVAL_SEQUENCE.toString().toLowerCase().startsWith(value.toLowerCase())) ||
                    (item.APPROVAL_DESCRIPTION && item.APPROVAL_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase())) ||
                    (item.APPROVAL_COMMENT && item.APPROVAL_COMMENT.toString().toLowerCase().startsWith(value.toLowerCase())) ||
                    (item.APPROVED_BY && item.APPROVED_BY.toString().toLowerCase().startsWith(value.toLowerCase())) ||
                    (item.APPROVAL_STATUS && item.APPROVAL_STATUS.toString().toLowerCase().startsWith(value.toLowerCase())) ||
                    (item.CREATED_DATE && item.CREATED_DATE.toString().toLowerCase().startsWith(value.toLowerCase()))

                let includesCondition =
                    (item.WORKFLOW_SETUP && item.WORKFLOW_SETUP.APPROVAL_SEQUENCE.toString().toLowerCase().includes(value.toLowerCase())) ||
                    (item.APPROVAL_DESCRIPTION && item.APPROVAL_DESCRIPTION.toString().toLowerCase().includes(value.toLowerCase())) ||
                    (item.APPROVAL_COMMENT && item.APPROVAL_COMMENT.toString().toLowerCase().includes(value.toLowerCase())) ||
                    (item.APPROVED_BY && item.APPROVED_BY.toString().toLowerCase().includes(value.toLowerCase())) ||
                    (item.APPROVAL_STATUS && item.APPROVAL_STATUS.toString().toLowerCase().includes(value.toLowerCase())) ||
                    (item.CREATED_DATE && item.CREATED_DATE.toString().toLowerCase().includes(value.toLowerCase()))
                    
                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {

        const { data, columns, value, filteredData } = this.state

        return (
            <>
                <Helmet><title>Approval Details</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Approval Details"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Approval Details</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <WorkflowApprovalDetailsTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <WorkflowApprovalDetailsModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowModuleTypeSearch={this.handleShowModuleTypeSearch}
                                        handleShowStatusSearch={this.handleShowStatusSearch}

                                        ApprovalType={this.state.APPROVAL_TYPE}

                                        ApprovalDescription={this.state.APPROVAL_DESCRIPTION}

                                        ApprovalComment={this.state.APPROVAL_COMMENT}
                                        handleChangeApprovalComment={e => this.setState({ APPROVAL_COMMENT: e.target.value })}
                                        
                                        ApprovalStatus={this.state.APPROVAL_STATUS}
                                        handleChangeApprovalStatus={e => this.setState({ APPROVAL_STATUS: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(WorkflowApprovalDetails));