import React from "react"
import { Button } from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit } from "react-feather"
import Can from '../../../../../utility/permissions/can';
class WorkFlowSetupTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    // disabled={!allowed || row.APPROVAL_STATUS === "APPROVED" || row.APPROVAL_STATUS === "REJECTED"}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Type",
                selector: "MODULE_TYPE",
                sortable: true,
                cell: row => <p className="mb-0">{row.WORKFLOW_SETUP.WORKFLOW.MODULE_TYPE.MODULE_NAME}</p>
            },
            {
                name: "Sequence",
                selector: "APPROVAL_SEQUENCE",
                sortable: true,
                cell: row => <p className="mb-0">{row.WORKFLOW_SETUP.APPROVAL_SEQUENCE}</p>
            },
            {
                name: "Description",
                selector: "APPROVAL_DESCRIPTION",
                sortable: true,
                cell: row => <p className="mb-0">{row.APPROVAL_DESCRIPTION}</p>
            },
            {
                name: "Comments",
                selector: "APPROVAL_COMMENT",
                sortable: true,
                cell: row => <p className="mb-0">{row.APPROVAL_COMMENT}</p>
            },
            {
                name: "Approved By",
                selector: "APPROVED_BY",
                sortable: true,
                cell: row => <p className="mb-0">{row.USER ? row.USER.USER_NAME : ""}</p>
            },
            {
                name: "Status",
                selector: "APPROVAL_STATUS",
                sortable: true,
                cell: row => <p className="mb-0">{row.APPROVAL_STATUS ? row.APPROVAL_STATUS : "PENDING"}</p>
            },
            {
                name: "Created Date",
                selector: "CREATED_DATE",
                sortable: true,
                cell: row => <p className="mb-0">{row.CREATED_DATE}</p>
            },
        ]
    }

    render() {

        return (
            <>
                <DataTable
                    className="dataTable-custom"
                    data={this.props.value.length ? this.props.filteredData : this.props.data}
                    columns={this.tableProperties.columns}
                    noHeader
                    pagination
                    subHeader
                    striped
                    responsive
                    subHeaderComponent={
                        <TableCustomHeader createButton={false} role={this.props.role} value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />
                    }
                />
            </>

        )
    }
}


export default WorkFlowSetupTable